import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import page from '@/components/common-components/page.vue';
import pageView from '@/components/common-ui/page-view.vue';
import { deepClone } from '@/until/index';
// 组件
import cImage from './component/image.vue';
import cSelect from './component/select.vue';
import cInput from './component/input.vue';
import cTip from './component/tip.vue';
import cTextarea from './component/textarea.vue';
import cNumber from './component/number.vue';
import cAddress from './component/address.vue';
let priceConfig = class priceConfig extends Vue {
    constructor() {
        super(...arguments);
        this.className = '业务配置';
        this.config = {
            label: "测试业务",
            spec: [],
            payOffConfig: {
                money: 2,
                per: 0
            },
            price: 1
        };
        this.setList = [];
        this.defaultOption = {
            input: {
                "type": "input",
                "label": ""
            },
            select: {
                "label": "下拉名称",
                "type": "select",
                "config": []
            },
            image: {
                type: 'image',
                url: ''
            },
            tip: {
                type: 'tip',
                title: '',
                content: ''
            },
            number: {
                label: '',
                type: 'number',
                number: 1,
                unit: '件',
                config: {
                    max: 10,
                    min: 1
                }
            },
            textarea: {
                label: '',
                type: 'textarea',
                placeholder: ''
            },
            address: {
                label: '',
                type: 'address'
            }
        };
    }
    updateData(updateData) {
        this.$set(this.setList, updateData.index, updateData.data);
    }
    get schoolInfo() {
        return this.$store.state.schoolInfo;
    }
    up(index) {
        this.moveTag('up', index);
    }
    down(index) {
        this.moveTag('down', index);
    }
    moveTag(state, index) {
        let upIndex = 0;
        if (state === 'up') {
            upIndex = index - 1;
        }
        else {
            upIndex = index + 1;
        }
        let currentItem = deepClone(this.setList[index]);
        let upItem = deepClone(this.setList[upIndex]);
        this.$set(this.setList, index, upItem);
        this.$set(this.setList, upIndex, currentItem);
    }
    showAction() {
        this.$createActionSheet({
            title: '选择组件',
            active: 0,
            data: [
                {
                    content: '下拉选',
                    type: 'select'
                },
                {
                    content: '内容搜集',
                    type: 'input'
                },
                {
                    content: '图片组件',
                    type: 'image'
                },
                {
                    content: '提示组件',
                    type: 'tip'
                },
                {
                    content: '数量组件',
                    type: 'number'
                },
                {
                    content: '输入框',
                    type: 'textarea'
                },
                {
                    content: '地址',
                    type: 'address'
                }
            ],
            onSelect: (item, index) => {
                this.$createToast({
                    txt: `Clicked ${item.content}`,
                    type: 'correct',
                    time: 1000
                }).show();
                let currentType = item.type;
                this.setList.push(this.defaultOption[currentType]);
            }
        }).show();
    }
    submitHandler(e) {
        e.preventDefault();
        this.config.spec = this.setList;
        this.$router.push({
            name: 'businessConfig',
            params: {
                data: deepClone(this.config)
            }
        });
    }
    saveSchoolInfo(type, value) {
        this.$store.dispatch('updateSchoolInfo', {
            schoolId: this.schoolInfo.id,
            type: type,
            value: value
        }).then((e) => {
            if (e.success) {
                this.showToastType();
            }
            this.$store.commit('SET_SCHOOL_INFO_BY_TYPE', {
                type: type,
                value: value
            });
        });
    }
    showToastType() {
        const toast = this.$createToast({
            txt: '保存成功',
            type: 'correct'
        });
        toast.show();
    }
    initModelData() {
        let params = this.$route.params;
        if (params.type) {
            if (params.type === 'add') {
            }
            else {
                let config = deepClone(params.data);
                this.config = config;
                this.setList = config.spec;
            }
        }
    }
    mounted() {
        this.initModelData();
    }
};
priceConfig = __decorate([
    Component({
        components: {
            page,
            pageView,
            cImage,
            cSelect,
            cInput,
            cTip,
            cTextarea,
            cNumber,
            cAddress
        }
    })
], priceConfig);
export default priceConfig;
